import React from 'react';



export class Comments extends React.Component {

singleCom(num){
    return(
        <div className="comment">
        <div className="commentText">{this.props.comText}</div>
        <div className="likesDiv">Likes: {this.props.likes}</div>
        <div className="funcButtons">
            <div onClick={()=>this.props.addLike()} className="addButton">+</div>
            <div onClick={()=>this.props.subLike()} className="subButton">-</div>
            <div onClick={()=>this.props.delComment()} className="delButton">X</div>
        </div>
        <div className="commentFoot">
        <div className="nickDiv">{this.props.name}</div>
        <div className="dateDiv">{this.props.data}</div>
        </div>
        </div>
    )
}

    render() {
        return (
            this.singleCom()
        )
    }
}
export default Comments