import React from 'react';

import Head from './head';
import Comments from './comments'
import Form from './form';

class App extends React.Component {
  state = {
    name: ['Simple Name'],
    comText: ['Simple TEXT'],
    data: ['13:05 22/02/2020'],
    likes:[0]
  }


  dataCalc() {
    let data = new Date()
    let month = data.getMonth() + 1 < 10 ? `0${data.getMonth() + 1}` : `${data.getMonth() + 1}`
    let day = data.getDate() < 10 ? `0${data.getDate()}` : `${data.getDate()}`
    let hours = data.getHours() < 10 ? `0${data.getHours()}` : `${data.getHours()}`
    let mins = data.getMinutes() < 10 ? `0${data.getMinutes()}` : `${data.getMinutes()}`
    return (
      `${hours}:${mins} ${day}/${month}/${data.getFullYear()}`
    )
  }



  rebuild() {
    let textQuery = document.getElementById("commentText").value;
    let nameQuery = document.getElementById("nameInput").value;
    this.setState({
      name: [...this.state.name, ...[nameQuery]],
      comText: [...this.state.comText, ...[textQuery]],
      data: [...this.state.data, ...[this.dataCalc()]],
      likes:[...this.state.likes,...[0]],
    })
    document.getElementById("commentText").value =''
    document.getElementById("nameInput").value =''
  };

  addLike(number){
   let handler = this.state.likes
   handler[number] = handler[number]+1
   this.setState({
     likes: handler
   })
  }

  subLike(number){
    let handler = this.state.likes
    handler[number] = handler[number]-1
    this.setState({
      likes: handler
    })
  }
  
  delComment(number){
    this.state.name.splice(number,1)
    this.state.data.splice(number,1)
    this.state.comText.splice(number,1)
    this.state.likes.splice(number,1)
    this.setState({
      name: this.state.name,
      data:this.state.data,
      comText:this.state.comText,
      likes:this.state.likes
   })
  }



  commentSingle(num) {
    return (
      <Comments
        key={num}
        name={this.state.name[num]}
        comText={this.state.comText[num]}
        data={this.state.data[num]}
        likes={this.state.likes[num]}
        addLike={() => {this.addLike(num)}}
        subLike={() => {this.subLike(num)}}
        delComment={() => {this.delComment(num)}}
      />
    )
  };

  commentsRendering() {
    let tableNames = []
    for (let i = 0; this.state.name.length > i; i++) {
      tableNames.push(this.commentSingle(i))
    }
    return (tableNames)
  };


  render() {
    return (
      <div>
        <Head />
        {this.commentsRendering()}
        <Form
          submitClick={() => {this.rebuild()}}
        />
      </div>
    )
  };
}
export default App;
