import React from 'react';




export default class Form extends React.Component {


    submited() {
        let textQuery = document.getElementById("commentText").value;
        let nameQuery = document.getElementById("nameInput").value;
        if (textQuery === '' || textQuery === null){
            alert("Comment field cannot be Empty!")
        } else{
            if (nameQuery === '' || nameQuery === null) {
                alert("Name field cannot be Empty!")
            } else {
                this.props.submitClick()
            }  
        }  
    }
            
    render() {
        return (
            <div className="formCont">
                    <div className="textArea">
                        <textarea id="commentText" rows="6" cols="40" placeholder="Add a comment..."  />
                    </div>
                    <div className="nameArea">
                        <input id="nameInput" type="text" placeholder="Name" />
                    </div>
                    <div className="submitButton">
                        <button onClick={()=>this.submited()}>Comment!</button>
                    </div>
            </div>
        )
    }
}